export default {
	data() {
		return {
			oldPwd: '',
			newPwdOne:'',
			newPwdTwo: '',
		}
	},
	async onLoad(event) {
		this.theMid = await this.getUserInfoWithKey('mid');
	},
	methods:{
		
		async clickSend(){
			if(this.oldPwd.length<=0){
				uni.showToast({
					icon:"none",
					title:'请输入旧密码'
				})
				return;
			}
			if(this.newPwdOne.length<=0){
				uni.showToast({
					icon:"none",
					title:'请输入新密码'
				})
				return;
			}
			
			if(this.newPwdTwo.length<=0){
				uni.showToast({
					icon:"none",
					title:'请再次输入新密码'
				})
				return;
			}
			
			if(this.newPwdOne !=this.newPwdTwo){
				uni.showToast({
					icon:"none",
					title:'两次新密码不一致,请重新输入'
				})
				return;
			}
			
			
			
			
			var theApi='Home/User/setPass';
			var theData={
					mid:this.theMid,
					oldpass:this.oldPwd,
					newpass:this.newPwdOne,
					reppass:this.newPwdTwo,

			};
			var res = await this.requestFromServer({
				url:theApi,
				data:theData,
			})
			if(res.code.toString()=='1'){
				uni.showToast({
					icon:null,
					title:res.message
				})
				
				setTimeout(function(){
					uni.navigateBack({delta:2})
				},2000)
			}else{
				uni.showToast({
					icon:null,
					title:res.message
				})
			}
			
			
		}
	}
}
